import axios from '../http'

/*
 * 
 */
//id
export const getCaptchaId = data => {
  return axios({
    url: '/admin/captcha-id',
    method: 'post',
    data
  })
}
// 
export const sigin = data => {
  return axios({
    url: '/admin/passport/signup',
    method: 'post',
    data
  })
}

// 
export const login = data => {
  return axios({
    url: '/admin/passport/login',
    method: 'post',
    data
  })
}

// 
export const logout = () => {
  return axios({
    url: '/admin/passport/logout',
    method: 'post'
  })
}

