import Vue from 'vue'
import VueRouter from 'vue-router'
import AdminTE from '../views/common/AdminTE.vue'

Vue.use(VueRouter)
const Login = () => import('../views/login/index.vue') //

const Sigin = () => import('../views/login/sigin.vue') //

const MeetIndex = () => import('../views/Meet/index.vue') //

const EditMeet=()=>import('../views/Meet/editMeet.vue') //


const UserInfo=()=>import('../views/UserInfo/index.vue') //

const MeetBlack=()=>import('../views/MeetBlack/index.vue') //

const MeetProc=()=>import('../views/MeetProc/index.vue') //

const routes = [
  {
    path: '*',
    redirect: '/login',
  },

  {
    path: '/login',
    component: Login,
  },
  {
    path: '/signin',
    component: Sigin,
  },
  {
    path: '/',
    component: AdminTE,
    name:AdminTE,
    children: [
      {
        path: '',
        redirect: '/MeetIndex'
      },
      {
        path: '/MeetIndex',
        component: MeetIndex,
        props: true
      },
      {
        path: '/EditMeet',
        component: EditMeet,
        props: true
      },
      {
        path: '/UserInfo',
        component: UserInfo,
        props: true
      },
      {
        path: '/MeetBlack',
        component: MeetBlack,
        props: true
      },
      {
        path: '/MeetProc',
        component: MeetProc,
        props: true
      },
    ]
  },

]

const router = new VueRouter({
  routes
})

export default router
